import { mapState } from 'vuex'
import moment from 'moment'
import OnEvent from '../common/OnEvent'

export default {
    extends: OnEvent,
    props: {
        propValue: {
            type: Object,
            require: true,
            default: () => {},
        },
        element: {
            type: Object,
            default: () => {},
        },
    },
    data() {
        return {
            diff: 0,
            timer: null,
            // unStart:  ongoing:  end:
            status: 'unStart',
        }
    },
    computed: {
        ...mapState([
            'editMode',
        ]),
        hour() {
            return Math.floor(this.diff / 1000 / 3600)
        },
        minutes() {
            return Math.floor(this.diff / 1000 / 60) % 60
        },
        seconds() {
            return Math.floor(this.diff / 1000) % 60
        },
    },
    watch: {
        'propValue.diff': function () {
            this.getTimestamp()
        },
        'propValue.starttime': function () {
            this.getTimestamp()
        },
        'propValue.endtime': function () {
            this.getTimestamp()
        },
    },
    filters: {
        // 
        timeFormat(value) {
            let str = value + ''
            if (str.length == 1) {
                return `0${str}`
            }
            return str
        },
    },
    methods: {
        getTimestamp() {
            let startUni = moment(this.propValue.starttime).unix()
            let endUni = moment(this.propValue.endtime).unix()
            let nowUni = moment().unix()
            console.log('')

            // 
            if (nowUni < startUni) {
                console.log('')

                this.status = 'unStart'
                this.countdownHandle()
            // 
            } else if (startUni <= nowUni && nowUni < endUni) {
                console.log('')

                this.status = 'ongoing'
                this.countdownHandle()
                // 
            } else {
                console.log('')
                this.status = 'end'
                clearTimeout(this.timer)
            }
        },
        // 
        countdownHandle() {
            // 
            this.diffTimeHandle()
            // 
            this.timer = setInterval(() => this.diffTimeHandle(), 1000)
        },

        diffTimeHandle() {
            // 
            let now = moment()
            // 
            let end = this.status == 'unStart' ? moment(this.propValue.starttime) : moment(this.propValue.endtime)
            // 
            this.diff = end.diff(now)

            if (this.diff <= 0) {
                clearTimeout(this.timer)
            }
        },
    },
}
